@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

body {
    padding: 0;
    margin: 0;
    background-color: black;
    color: white;
    font-family: 'Press Start 2P';
}
.App {

}

.main {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 600px;
    margin: 30px auto;
    color: white;
    font-family: 'Press Start 2P';
    position: relative;
}
.control {
    flex: 1;
}
.control h2 {
    text-transform: uppercase;
    text-align: center;
    font-size: 32px;
}
.control_info {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.control_UI {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.control_UI button {
    border: 0;
    width: 100%;
    max-width: 150px;
    height: 40px;
    margin: 15px auto;
    text-transform: uppercase;
    font-family: 'Press Start 2P';
}
.control_UI button:hover {
    box-shadow: 0 0px 10px 2px rgb(29, 255, 78);
    transition: all 0.2s;
}
.control_message__gameover {
    text-transform: uppercase;
    display: block;
    width: 52%;
    color: red;
    font-size: 30px;
    position: absolute;
    text-align: center;
    top: 270px;
    left: 0px;
}
 @media (max-width: 768px) {
    .main {
        flex-direction: column-reverse;
    }
    .control_info {
        flex-direction: row;
        justify-content: center;
        gap: 10px;
    }
    
    .control_UI {
        flex-direction: row;
        justify-content: center;
    }
    .control_UI button {
        margin-bottom: 20px;
    }
    canvas {
        max-width: 80%;
    }
    .control_message__gameover {
        
    }
 }
 @media (max-width: 425px) {
    .main {
        flex-direction: column-reverse;
        margin: 10px auto;
    }
    .control_info,
    .control_UI {
        flex-direction: column;
        justify-content: space-around;
        gap: 5px;
    }
    .control_info div {
        height: 15px;
    }
    .control_UI button {
        margin-bottom: 10px;
    }
    canvas {
        max-width: 80%;
    }
    .control_message__gameover {
        top: 70%;
        width: 100%;
    }
 }